.headerf {
    margin-bottom: 100px;
    z-index: 5;

}

.headerf header{
    margin: 20px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.headerf  p{
    color: white;
}

.headerf  .logoside img{
    width: 160px;
}

.headerf  .profile img{
    width: 90px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    border: 10px solid #151419;
    z-index: 2;
}

.headerf header .profile{
    display: flex;
    align-items: center;
}

.headerf  .profile .status .exp{
    display: flex;
    align-items: center;
    margin-top: 10px;
}

.headerf  .profile .status .exp .bar{
    background-color: #151419;
    color: white;
    position: relative;
    right: -18px;
    border: 5px solid #151419;
    border-right-width: 8px;
    box-sizing: content-box;
}

.headerf  .profile .status .level{
    align-self: flex-end;
    background-color:#151419 ;
    color: white;
    padding-right:15px ;
    position: relative;
    right: -10px;
    bottom: -5px;
    border-radius: 8px 0 0 8px;
    -webkit-border-radius: 8px 0 0 8px;
    -moz-border-radius: 8px 0 0 8px;
    -ms-border-radius: 8px 0 0 8px;
    -o-border-radius: 8px 0 0 8px;
}

.headerf  .profile .status .level span{
    background-color: #272337;
    padding: 5px;
    display: inline-block;
    border-radius: 8px 0 0 8px;
    -webkit-border-radius: 8px 0 0 8px;
    -moz-border-radius: 8px 0 0 8px;
    -ms-border-radius: 8px 0 0 8px;
    -o-border-radius: 8px 0 0 8px;
}

.headerf  .profile .status{
    display: flex;
    flex-direction: column;
}

.headerf  .profile .status .exp span{
    background-color: #272337;
    padding:5px;
    position: relative;
    color: white;
    border-radius: 8px 0 0 8px;
    -webkit-border-radius: 8px 0 0 8px;
    -moz-border-radius: 8px 0 0 8px;
    -ms-border-radius: 8px 0 0 8px;
    -o-border-radius: 8px 0 0 8px;
    right: -18px;

}

.bar{
    width: 100px;
    height: 20px;
    background-color: rgb(209, 209, 209);
    position: relative;
}

.bar .progress{
    height: 100%;
    background-color: violet;
    width: 0;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.bar .progressdetails{
    position: absolute;
    background-color: #151419;
    color: white;
    padding: 5px;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    margin: 10px 0 0 -5px;
    opacity: 0;
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
}

.bar:hover .progressdetails{
    opacity: 100%;
}

