header .barburger{
    display: none;
}

.barburger{
    width: fit-content;
    cursor: pointer;
    display: none;
}

.barburger .slice{
    width: 60px;
    height: 5px;
    background-color: white;
    margin: 10px;
    border-radius: 50px;
    -webkit-border-radius: 50px;
    -moz-border-radius: 50px;
    -ms-border-radius: 50px;
    -o-border-radius: 50px;
    box-shadow: 1px 2px 8px 0 rgba(0, 0, 0, 0.3);
    transition: transform 0.3s ease 0s;
}

.profile{
    position: relative;
}

.profile i{
    position: absolute;
    right: -5px;
    z-index: 3;
    color: red;
    font-size: 25px;
    bottom: 0;
    cursor: pointer;
}

@media (min-width:1200px)
{
    nav ul{
        font-size: 30px;
        padding: 5px;
    }
}

@media (max-width:768px) {
    .headerf  .logoside img {
        width: 120px;
    }

    .headerf  p {
        color: white;
        font-size: 12px;
    }

    .headerf  .profile .status {
        display: flex;
        flex-direction: column;
        width: 130px;
    }

    .headerf  .profile .status .level {
        align-self: flex-end;
        background-color: #151419;
        color: white;
        padding-right: 15px;
        position: relative;
        right: -10px;
        bottom: -7px;
        border-radius: 8px 0 0 8px;
        -webkit-border-radius: 8px 0 0 8px;
        -moz-border-radius: 8px 0 0 8px;
        -ms-border-radius: 8px 0 0 8px;
        -o-border-radius: 8px 0 0 8px;
    }

    .headerf  .profile .status .level span {
        background-color: #272337;
        padding: 5px;
        display: inline-block;
        border-radius: 8px 0 0 8px;
        -webkit-border-radius: 8px 0 0 8px;
        -moz-border-radius: 8px 0 0 8px;
        -ms-border-radius: 8px 0 0 8px;
        -o-border-radius: 8px 0 0 8px;
    }

    .headerf  .profile .status .exp {
        display: flex;
        align-items: center;
        margin-top: 10px;
    }

    .headerf .profile .status .exp span {
        background-color: #272337;
        padding: 5px;
        position: relative;
        color: white;
        border-radius: 8px 0 0 8px;
        -webkit-border-radius: 8px 0 0 8px;
        -moz-border-radius: 8px 0 0 8px;
        -ms-border-radius: 8px 0 0 8px;
        -o-border-radius: 8px 0 0 8px;
        right: -18px;
    }

    .bar {
        width: 80px;
        height: 20px;
    }

    .headerf  .profile img {
        width: 80px;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
        border: 10px solid #151419;
        z-index: 2;
    }

    nav ul{
        flex-direction: column;
        text-align: right;
        position: absolute;
        right: -110%;
        background-color: #272337;
        padding: 20px;
        font-size: inherit;
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
        z-index: 5;
        

}

    nav ul.active{
        right: 0;
        display: flex;
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
        z-index: 5;

    }

    nav ul li{
        margin-bottom: 10px;
        font-size: 31px;
    }

    nav .profile{
        margin-bottom: 10px;
        display: flex;
        align-items: center;
    }
        
    .headerf header .profile{
        display: none;
    }
        
    header .barburger{
        display: block;
    }

    body{
        overflow-x:hidden ;
    }

        
    .activeline2{
        position: relative;
        transform: scaleX(0);
        transition: transform 0.3s ease 0s;
        -webkit-transition: transform 0.3s ease 0s;
        -moz-transition: transform 0.3s ease 0s;
        -ms-transition: transform 0.3s ease 0s;
        -o-transition: transform 0.3s ease 0s;
        -webkit-transform: scaleX(0);
        -moz-transform: scaleX(0);
        -ms-transform: scaleX(0);
        -o-transform: scaleX(0);
    }

    .activeline1{
        position: relative;
        transform: rotate(-45deg) translate(-10px, 10px);
        -webkit-transform: rotate(-45deg) translate(-10px, 10px);
        -moz-transform: rotate(-45deg) translate(-10px, 10px);
        -ms-transform: rotate(-45deg) translate(-10px, 10px);
        -o-transform: rotate(-45deg) translate(-10px, 10px);
    }

    .activeline3{
        position: relative;
        transform: rotate(45deg) translate(-10px, -10px);
        -webkit-transform: rotate(45deg) translate(-10px, -10px);
        -moz-transform: rotate(45deg) translate(-10px, -10px);
        -ms-transform: rotate(45deg) translate(-10px, -10px);
        -o-transform: rotate(45deg) translate(-10px, -10px);
    }
    
    nav{
        width: 250px;
        position: absolute;
        height: 450px;
        overflow-x: hidden;
        right: 0;
    }
}
