.loginp button{
    cursor: pointer;
    outline: none;
    border: none;
    display: block;
    color: white;
    padding: 20px;
    margin: 10px 0;
    width: 100%;
    border-radius: 50px;
}

.loginp .google-btn{
    background-color: #ff3f34;
}

.loginp .face-btn{
    background-color: #4bcffa;
}

.loginp img{
    width: 50%;
    height: 50%;
}

.loginp .theform{
    width: 50%;
}


@media (max-width:768px) {
    .loginp{
        flex-direction: column;
        width: 100%;
    }
    .loginp .theform{
        width: 100%;
    }
    .loginp img{
        width: 100%;
    }
}


.wavel,.waver{
    position: absolute;
    width: 50%;
}

.waver{
    right: 0;
    top: 0;
}

.wavel{
    left: 0;
    bottom: 0;
}


@media (max-width:1200px) {
    .wavel,.waver{
        position: absolute;
        width: 60%;
    }
    
}

.loginp h1{
    font-size: 70px;
    color: #e0e9ef;
    margin-bottom: 10px;
}

.loginp h2{
    color: #bec3c7;
    direction: rtl;
    text-align: center;
    font-size: 16px;
    margin: 20px 0;
}